<template>
  <section class="d-flex flex-column align-items-center justify-content-center h-100">
    <div class="mb-3">
      <VCalendar :attributes="attributes" class="border-0" />
    </div>
    <h4>{{ date.week }} {{ date.today }} {{ date.dateMonth }}</h4>
    <h5>8<span class="timePoint">:</span>00AM - 6<span class="timePoint">:</span>00PM</h5>
  </section>
</template>

<script>
import { gsap } from 'gsap';

export default {
  data() {
    return {
      week: ['SUN', 'MON', 'TUES', 'WED', 'THURS', 'FRI', 'SAT'],
      date: {
        dateYears: '',
        dateMonth: '',
        today: '',
        week: '',
      },
      attributes: [
        // {
        //   key: 'today',
        //   highlight: {
        //     color: 'purple',
        //     fillMode: 'solid',
        //     contentClass: 'italic',
        //   },
        //   dates: new Date(2023, 12, 12),
        // },
        // {
        //   highlight: {
        //     color: 'purple',
        //     fillMode: 'light',
        //   },
        //   dates: new Date(2023, 12, 13),
        // },
        {
          highlight: {
            color: 'gray',
            fillMode: 'outline',
          },
          dates: new Date(),
        },
      ],
    };
  },
  mounted() {
    const newDate = new Date();
    this.date.dateYears = newDate.getFullYear();
    this.date.dateMonth = newDate.toLocaleString('en-US', { month: 'short' });
    this.date.today = newDate.getDate();
    this.week.forEach((item, i) => {
      if (i === newDate.getDay()) {
        this.date.week = item;
      }
    });
    gsap.to('.timePoint', { opacity: 0, duration: 1.4, repeat: -1 });
  },
};
</script>
